import * as React from 'react';
import { Layout, Table, Tabs, Button, } from 'antd';
import "./people.scss";
import request, { get } from '../../utils/request';
import QRCode from 'qrcode.react';
import classnames from "classnames";
import AddInput from './addInput';
import { connect } from 'dva';
import { Modal } from 'antd';
const { Content } = Layout;
const { TabPane } = Tabs;

const NAMESPACE = 'Basis';
const mapStateToProps = (state: any) => {
  return {
    userInfo: state[NAMESPACE].userInfo
  }
}

const renderContent = (value: any, row: any, index: any) => {
  const obj = {
    children: value,
    props: {} as any,
  };
  if (index === 4) {
    obj.props.colSpan = 0;
  }
  return obj;
};
const columns = [{
  width: 150,
  align: "center" as "center",
  title: '',
  dataIndex: 'title',
  render: (text: any) => <a href="javascript:;">{text}</a>,
}, {
  width: 150,
  render: (text: any, row: any, index: any) => {
    if (index < 4) {
      return <a href="javascript:;">{text}</a>;
    }
    return {
      children: <a href="javascript:;">{text}</a>,
      props: {
        colSpan: 2,
      },
    };
  },

  align: "center" as "center",
  title: '',
  className: 'column-money',
  dataIndex: 'price',
}, {
  width: 150,
  align: "center" as "center",
  title: '',
  dataIndex: 'description',
  render: renderContent
},
]
@connect(mapStateToProps)
export default class People extends React.Component<any, any> {
  state = {
    data: [],
    current_length: 0,
    count: 20,
    visible: false,
    order_no: '',
    sku_id: 1,
  };
  tmp_id = 1;
  timer = 1;
  componentDidMount() {
    (async () => {
      const result = await get('/api/getProduct?product_id=1');
      const order_no = await get('/api/getOrderNo', { resToJson: false });
      this.setState({
        data: result.skus,
        order_no: order_no
      })
    })();
    this.timer = window.setInterval(
        () => this.getOrderPaid(), 3000
    );
  }
  componentWillUnmount() {
    window.clearInterval(this.timer);
  }
  getOrderNo() {
    (async () => {
      const order_no = await get('/api/getOrderNo', { resToJson: false });
      this.setState({
        order_no: order_no
      })
    })()
  }
  getOrderPaid() {
    (async () => {
      const order_paid = await request('/api/getPaidByOrderNo', {
        method: 'POST',
        resToJson: false,
        body: {
          order_no: this.state.order_no,
        }
      });
      console.log(order_paid);
      if (order_paid.code == 1) {
        window.clearInterval(this.timer);
        this.showModal();
      } else if(order_paid.code == 2){
        window.clearInterval(this.timer);
        //
      }
    })()
  }
  footSkuChange = (length: any) => {
    this.getOrderNo();
    this.setState({
      length,
      current_length: length
    })
  }
  handleAmountChange = (count: any) => {
    this.getOrderNo();
    if(count <=100){
    this.tmp_id = 1;
    }else if(count <=200){
    this.tmp_id = 2;
    }
    else if(count <=300){
    this.tmp_id = 3;
    }
    else if(count <=400){
    this.tmp_id = 4;
    }
    else if(count <=500){
    this.tmp_id = 5;
    }
    this.setState({
      count,
      sku_id: this.tmp_id
    })
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e: any) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e: any) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  render() {
    const { userInfo } = this.props;    
    const { data, current_length, count, order_no,sku_id } = this.state;
    const qrcodeurl = `https://pst.pingshentong.com/pay?sku_id=${sku_id}&length=${current_length}&amount=${count}&user_id=${userInfo && userInfo.id}&order_no=${order_no}$t=1111`;
    const price = 299;
    const amount = current_length / 12 * price * count;

    return (
      <Content className="buy-people" style={{ background: "white", padding: '30px 20px' }}>
        <div className="title">
          1<a></a>
       </div>
        <Table
          size="middle"
          pagination={false}
          columns={columns}
          dataSource={data}
          bordered
        />
        <div className="title">
          3

       </div>
        <AddInput onChange={this.handleAmountChange} count={count}> <s>2010</s></AddInput>
        <div className="month">
          <Button type="default" className={classnames("btn-month", current_length === 1 ? "active" : "")} onClick={() => this.footSkuChange(1)} >1</Button>
          <Button type="default" className={classnames("btn-month", current_length === 2 ? "active" : "")} onClick={() => this.footSkuChange(2)} >2</Button>
          <Button type="default" className={classnames("btn-month", current_length === 3 ? "active" : "")} onClick={() => this.footSkuChange(3)} >3</Button>
        </div>
        <div className="title">4</div>
        <div className="pay-name">
          <Tabs defaultActiveKey="1">
            <TabPane tab="" key="1" style={{ padding: '24px' }}>
              <div style={{ overflow: 'hidden' }}>
                <div className="code">
                  <div className="ewm" style={{ marginLeft: '12px' }}>
                    <QRCode value={qrcodeurl} />
                  </div>
                  <div style={{ display: 'block', marginTop: '10px' }}>

                  </div>

                </div>
                <div className="pay">
                  <div className="introduce">
                    <a style={{ textDecoration: 'line-through' }}>280</a>
                  </div>
                  <div className="introduce">
                    <a>{amount}</a>
                  </div>
                  <div className="zhifu"></div>
                  <div className="weixin"></div>
                  <p style={{ fontSize: '14px', paddingTop: '20px' }}>/</p>
                </div>
              </div>
            </TabPane>
            <TabPane tab="" key="2">
              <div style={{ padding: '24px' }}>
                <div>
                  <span className="heading">:</span>
                  <span className="content"></span>
                </div>
                <div>
                  <span className="heading">:</span>
                  <span className="content">1708326009100002420</span>
                </div>
                <div>
                  <span className="heading">:</span>
                  <span className="content"></span>
                </div>
                <div>
                  <span className="heading"></span>
                  <span className="price">
                    <a>280</a> 
               <span>280</span>
                  </span>
                </div>
                <div>
                  <span className="heading">
                    <Button type="primary"></Button>
                  </span>
                  <span className="date">
                    1~23~5
              </span>
                </div>
                <div>
                  <p className="prompt">
                    
                  </p>
                  <p className="prompt"></p>
                  <p className="prompt">pingshentong@163.com</p>
                  <p className="prompt">0374-6700996</p>
                </div>
              </div>
            </TabPane>
          </Tabs>
        </div>
        <p style={{ margin: '12px 0px 200px 28px' }}></p>
        <div>
          <Button type="primary" onClick={this.showModal} >
            </Button>
        </div>
        <Modal
          title=""
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={null}
        >
          <p style={{ textAlign: 'center', marginBottom: '60px' }}></p>
          <div style={{ textAlign: "center" }}>
            <a href="#" style={{ marginRight: '20px' }}></a>
            <a href="#"></a>
          </div>
        </Modal>

      </Content>

    )

  }
}
